.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;

  & button {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
      'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.5;
    padding: 10px 15px;
    margin-right: 10px;
    border-radius: 10px;
  }
}
